* {
  margin: 0;
  padding: 0;
}

.App {
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}

.container {
  width: 500px;
  height: 300px;
  background-color: hsl(217, 19%, 24%);
  border-radius: 20px;
}

.container p {
  text-align: center;
  margin: 0 40px 0px 40px;
}

.container span {
  color: hsl(150, 100%, 66%);
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
}

#advice-id {
  margin-top: 20px;
  margin-bottom: 20px;
}

.divider {
  display: flex;
  justify-content: center;
}

.divider img {
  position: relative;
  top: 35px;
}

button {
  background-color: hsl(150, 100%, 66%);
  padding: 20px;
  border-radius: 50%;
  border: none;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  top: 72px;
  cursor: pointer;
  transition: 0.3s ease;
}

button:hover {
  box-shadow: 0 0 30px 10px rgba(0,168,84,0.68);
}

.text-container {
  height: 100px;
  margin-bottom: 15px;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
}

@media screen and (max-width: 500px) {
  .App {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
  }
  
  .container {
    max-width: 330px;
    height: 400px;
  }

  .divider img {
    content: url(./images/pattern-divider-mobile.svg);
    position: relative;
    top: 30px;
    width: 80%;
  }

  button {
    top: 60px;
  }

  .text-container {
    height: 200px;
  }
}